import React from "react";
import NavBar from './NavBar/NavBar.js'
import Gallery from './Gallery/Gallery.js'
import './App.css';
import About from "./About/About";
import Contact from "./Contact/Contact";
import Footer from "./Footer/Footer";
import Welcome from "./Welcome/Welcome";
import Burger from "./Burger/Burger";
import Pbanner from "./ParallaxBanner/Pbanner";
import Up from "./Up/Up";
import { FullPage, Slide } from 'react-full-page';



const App = () => {
    return (
        <div className="tc">
            <FullPage duration='300'>
            <Slide><section id="NavBar"><NavBar /></section>
            <div className="b1"><Burger/></div>
            <Up/>
            <section id="Welcome"><Welcome /></section><Pbanner/></Slide>
            <Slide><section id="About"><About /></section></Slide>
            <Slide><section id="Gallery"><Gallery /></section></Slide>
            <Slide><section id="Contact"><Contact /></section><Footer/></Slide>
            </FullPage>
        </div>
    )
}

export default App